

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.nav-ul{
  padding: 5px;
  margin: 0px;
  background-color: skyblue;
}
.nav-ul li{
  display: inline-block;
  padding: 10px;
}

.nav-ul li a{
  text-decoration: none;
  color: #fff;
  padding: 5px;
  
}
.SignupBox{
  margin-left: 30%;

}

.signheading{
  margin-left: 18%;
}

.inputbox{
  display: block;
  border: 1px solid skyblue;
  padding: 10px;
  height: 25px;
  width: 350px;
  margin: 20px;
}

.submitbutton{
  background-color: skyblue;
  padding: 10px;
  height: 40px;
  width: 70px;
  border: 1px solid;
  color: rgb(36, 35, 35);
  cursor: pointer;
  margin-left: 20px;

}
.footer{
  position: absolute;
  bottom: 0px;;
  width: 100%;
  background-color: skyblue;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
